.tabbar {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-shadow: 0 -7px 10px rgba(0, 0, 0, .1);
          box-shadow: 0 -7px 10px rgba(0, 0, 0, .1);
  padding: 2px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.tab-item {
  text-align: center;
}
.tab-item.actived {
  color: #3879d9;
}
.tab-item__img {
  font-size: 24px;
}
.tab-item__title {
  margin: 2px;
  font-weight: normal;
  font-size: 16px;
}


